<template>
  <div class="filter-nav-bar">
		<div class="item" v-for="(item,index) in navArr" :key="index"  :class="{active:currentIndex==index}">
			<div class="text-box" @click="caozuoClick(index)" :class="{navactive:item.active}">{{item.title}}</div>
		</div>
  </div>
</template>

<script>
  export default {
    name: "FilterNavBar",
	data(){
		return{
			currentIndex:-1
		}
	},
	props:{
		navArr:{
			type:Object,
			default(){
				return {}
			}
		}
	},
	methods:{
		caozuoClick(index){
			if(this.currentIndex!=index){
				this.currentIndex=index
			}else{
				this.currentIndex=-1
			}
			this.$emit("caozuoClick",this.currentIndex)
		}
	}
  }
</script>

<style scoped>
.filter-nav-bar{
	display:flex;
}
.item{
	display:line-block;
	text-align:center;
	font-size:13px;
	padding:8px 0px;
	border-bottom:1px solid #eaeaea;
	border-right:1px solid #fff;
	border-left:1px solid #fff;
	width:50%;
}
.text-box{
	display:inline-block;
	background:#eaeaea;
	padding:8px 0;
	border-radius:5px;
	color:#6d6d6d;
	line-height:16px;
	width:80px;
}

.active{
	background:#fff;
	border-right:1px solid #ccc;
	border-left:1px solid #ccc;
	border-bottom:none;
	border-radius:5px;
}
.navactive{
	color:#ff5a5a;
	border:1px solid #ff5a5a;
	background:#fff;
}
</style>
