<template>
	<div id="filter-nav-box">
		<div class="center-box">
			<div class="item" v-for="(item,index) in comTest" :key="index"  @click="itemClick(index)">
				<span class="text-box" :class="{active_goods:currentIndex==index}">{{item}}</span>
			</div>
		</div>
		<div class="bottom-box">
			<div class="reset" @click="resetClick">
				重置
			</div>
			<div class="sure" @click="successClick">
				确定
			</div>
		</div>
	</div>
</template>

<script>

export default {
  name: 'FilterNavBox',
 components:{
	
 },
  props:{
	test:{
		type:Object,
		default(){
			return {}
		}
	}
  },
  data(){
	return{
		currentIndex:-1
	}
  },
  computed:{
	comTest(){
		return this.test.arr
	}
  },
  mounted(){
	//需要查看是否存在
	console.log(this.$store.state.filters[this.test.type])
  },
  methods:{
	itemClick(index){
		if(this.currentIndex===index){
			this.currentIndex=-1
		}else{
			this.currentIndex=index
		}
		//发送事件到父组件
		this.$emit("FilterNavBoxClick",this.currentIndex)
		//console.log(this.test.type)
		
	},
	resetClick(){
		this.currentIndex=-1
	},
	successClick(){
		//console.log("完成")
		this.$emit("successClick")
	}
	//这里判断一个状态是否已经有一个选中项
  },
}
</script >

<style  scoped>
#filter-nav-box{
	border-bottom:1px solid #ccc;
	z-index:10;
	width:100%;
	background:#fff;

}
.center-box{
	border-bottom:1px solid #f1f1f1;
	padding:10px 0;
	overflow:hidden;
}
.item{
	max-width:50%;
	width:46%;
	float:left;
	text-align:center;
	margin-left:10px;
	margin-top:10px;
}
.text-box{
	background:#eaeaea;
	display:inline-block;
	line-height:16px;
	padding:8px 0;
	width:100%;
	font-size:14px;
	border:1px solid #f1f1f1;
}
.bottom-box{
	display:flex;
	line-height:50px;
	height:50px;
}
.reset{
	width:35%;
	text-align:center;
	color:black;
}
.sure{
	background:#ff5959;
	width:65%;
	text-align:center;
	color:white;
}
.active_goods{
	border:1px solid #ff5959;
	background:#fff;
	color:#ff5959;
}

</style>