<script>

import NavBar from 'components/common/navbar/NavBar'
import TopNavBar from 'components/contents/topnavbar/TopNavBar'
import FilterNavBar from 'components/contents/filternavbar/FilterNavBar'
import Scroll from 'components/common/scroll/Scroll'

import AllGoodsContent from './childComps/AllGoodsContent'
import FilterNavBox from './childComps/FilterNavBox'

export default {
  name: 'AllGoods',
  components: {
	NavBar,
	TopNavBar,
	FilterNavBar,
	AllGoodsContent,
	Scroll,
	FilterNavBox
  },
  data(){
	return{
		title:'',
		navArr:[
			{title:'最新'},
			{title:'人气'},
			{title:'收藏'},
			{title:'价格'},
		],
		filterNavArr:{
				systemType:{title:'',active:true,id:0},
				level:{title:'',active:true,id:1},
				pingfen:{title:'',active:true,id:2},
				price:{title:'',active:true,id:3},
		},
		currentType:'tohdayOrder',
		isDis:false,
		tabOffsetTop:0,
		isFilterDis:false,
		textArrType:'systemType',
		//vuex的属性
		textArr:[],			//所有商品头部点击菜单
		defaultNavArr:['服务器','等级','评分','价格']
		//{{this.$route.params.test}}
	}
  },
	methods:{
		navClick(index){
			//console.log(this.navArr[index].title)
						//这里获取内容
			switch(index){
				case 0:
					this.currentType="tohdayOrder"
					break;
				case 1:
					this.currentType="waitSendGoods"
					break;
				case 2:
					this.currentType="waitPayMoney"
					break;
				case 3:
					this.currentType="allOrders"
					break;
			}
			//console.log(this.currentType)
		},
		//监听滚动
		scroll(position){
			this.isDis=(-position.y)>=(this.tabOffsetTop)
			//this.isFilterDis=0
			if(!this.isDis){
				this.isFilterDis=0
				this.$refs.filterNav1.currentIndex=-1
				this.$refs.filterNav2.currentIndex=-1
			}
		},
		//加载更多
		loadMove(){
			console.log("加载更多")
		},
		//点击按钮
		caozuoClick(index){
			this.isFilterDis=index
			//console.log(index)
			if(index==-1){
				this.isFilterDis=0
			}else{
				this.textArrType=index
			}
			//判断是否已经有选中了
			this.$refs.filterbox.currentIndex=this.$store.state.btnArrs[this.textArrType].arr.findIndex(item=>item==this.$store.state.filters[this.textArrType])
		},
		allClick(){
			this.isFilterDis=this.$refs.filterNav1.currentIndex
		},
		//2级菜单点击-这里是子按钮的点击
		FilterNavBoxClick(index){
			//改变一个vuex的值-需要类型和值
			//修改内容
			//console.log(index)

			let btnArrs=this.$store.state.btnArrs
			//console.log(btnArrs[this.textArrType].arr[index])		//当前按钮
			//这两个点击完成才改变
			this.$store.state.filters[this.textArrType]=btnArrs[this.textArrType].arr[index]
			console.log(this.$store.state.filters[this.textArrType])
			
			this.filterValue(this.filterNavArr[this.textArrType],btnArrs[this.textArrType].arr[index],btnArrs[this.textArrType].title)
			//console.log(this.$store.state.filters)
		},
		//处理是否vuex里面是有储蓄选中筛选
		/*filterValue(){
			let filter=this.filterNavArr
			filter['level'].title=this.$store.state.filters['level']?this.$store.state.filters['level']:"等级"
			filter['level'].active=this.$store.state.filters.['level']?true:false
			filter['pingfen'].title=this.$store.state.filters.['pingfen']?this.$store.state.filters['pingfen']:"评分"
			filter['pingfen'].active=this.$store.state.filters.['pingfen']?true:false
			filter['systemType'].title=this.$store.state.filters.['systemType']?this.$store.state.filters['systemType']:"服务器"
			filter['systemType'].active=this.$store.state.filters.['systemType']?true:false
			filter['price'].title=this.$store.state.filters.['price']?this.$store.state.filters['price']:"价格"
			filter['price'].active=this.$store.state.filters.['price']?true:false
		}*/
		//优化
		filterValue(filter,value,title){
			filter.title=value?value:title
			filter.active=value?true:false
		},
		//完成按钮
		successClick(){
			//console.log("完成")
			this.isFilterDis=0
			this.$refs.filterNav1.currentIndex=-1
			this.$refs.filterNav2.currentIndex=-1
			//每点击一次发送一次请求-改变this.currentIndex的值
		},
		//返回按钮
		backClick(){
			this.$router.push("/category").catch(err=>{err})
		}
	},
  computed:{
	ordersArr(){
		return this.$store.state.collectList[this.currentType].list
	},
	ctextArr(){
		return this.$store.state.btnArrs[this.textArrType]
	}
	
  },
   mounted(){
	this.tabOffsetTop=this.$refs.filterNav2.$el.offsetTop
	//刷新一下页面
	this.$refs.scroller.refresh();		//刷新一下
		this.$refs.scroller.finishPullUp()	//获取最新高度
  },
  created(){
	let filter=this.filterNavArr
	let stateFilter=this.$store.state.filters
	this.filterValue(filter['systemType'],stateFilter['systemType'],this.defaultNavArr[0])
	this.filterValue(filter['level'],stateFilter['level'],this.defaultNavArr[1])
	this.filterValue(filter['pingfen'],stateFilter['pingfen'],this.defaultNavArr[2])
	this.filterValue(filter['price'],stateFilter['price'],this.defaultNavArr[3])
  },

}
</script >

