<template>
  <div class="top-nav-bar">
		<div v-for="(item,index) in navArr" :key="index" @click="navClick(index)">
			<span :class="{active:currentIndex==index}">{{item.title}}</span>
		</div>
		<div class="fliter" @click="filterClick">
			<span>筛选</span>
		</div>
  </div>
</template>

<script>
  export default {
    name: "TopNavBar",
	data(){
		return{
			currentIndex:0
		}
	},
	props:{
		navArr:{
			type:Array,
			default(){
				return []
			}
		}
	},
	methods:{
		navClick(index){
			this.currentIndex=index
			this.$emit('navClick',index)
		},
		filterClick(){
			//console.log("筛选")
			this.$router.push("/filterpage").catch(err=>{err})
		}
	}

  }
</script>

<style scoped>
.top-nav-bar{
	display:flex;
}

.top-nav-bar div{
	flex:1;
	text-align:center;
	line-height:40px;
	border-bottom:1px solid #f1f1f1;
	color:black;
	font-size:14px;
}
.active{
	color:red;
}
.fliter{
	border-left:1px solid #f1f1f1;
}
</style>
